#main-img{
   border-radius: 50%;
}

aside{
   width: 100%;
   z-index: 1000;
}

.main-menu{
   background-color: rgba(0, 4, 255);
   height: 320px;
   width: 100%;
   overflow: hidden;
   color: white;
   -webkit-transition: all .5s ease;
   -o-transition: all .5s ease;
   transition: all .5s ease;
}

.main-menu.open{
   height: 560px;
}

@media (max-width: 767px){
   .hide-desktop-menu{
      display: none;
   }   
}

@media (min-width: 768px) {
   aside{
      width: 20px;
      -webkit-transition: width 300ms linear;
      -o-transition: width 300ms linear;
      transition: width 300ms linear;
      min-height: calc(100vh);
   }
   aside.open{
      width: 16.666667%;
      -webkit-transition: width 300ms linear;
      -o-transition: width 300ms linear;
      transition: width 300ms linear;
   }

   /*main menu*/
   .main-menu {
      width: 0px;
      background-color: rgba(0, 4, 255, 0.4);
      overflow: hidden;
      -webkit-transition: width 310ms linear;
      -o-transition: width 310ms linear;
      transition: width 310ms linear;
      height: calc(100%);
      position: relative;
   }
   .main-menu.open {
      width: 94%;
      -webkit-transition: width 310ms linear;
      -o-transition: width 310ms linear;
      transition: width 310ms linear;
      height: calc(100%);
   }

   i {
      border: solid black;
      border-width: 0 3px 3px 0;
      display: inline-block;
      padding: 3px;
  }
  
  .right {
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg);
  }
  
  .left {
      -ms-transform: rotate(135deg);
          transform: rotate(135deg);
      -webkit-transform: rotate(135deg);
  }
  
  .up {
      -ms-transform: rotate(-135deg);
          transform: rotate(-135deg);
      -webkit-transform: rotate(-135deg);
  }
  
  .down {
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
      -webkit-transform: rotate(45deg);
  }
}


