.maxheight{
   height: calc(100%);
}

.minheight{
   min-height: calc(100vh);
}

html{
   min-height: calc(100vh);
   background: -webkit-gradient(linear, left top, left bottom, from(#7004aa), color-stop(rgb(6, 124, 6)), color-stop(rgb(34, 216, 28, .4)), to(#7004aa));
   background: -webkit-linear-gradient(#7004aa, rgb(6, 124, 6), rgb(34, 216, 28, .4), #7004aa);
   background: -o-linear-gradient(#7004aa, rgb(6, 124, 6), rgb(34, 216, 28, .4), #7004aa);
   background: linear-gradient(#7004aa, rgb(6, 124, 6), rgb(34, 216, 28, .4), #7004aa);
   background-repeat: no-repeat;
}

body {
   background: transparent;
   font-family: 'Crimson Text', serif;
   font-size: 20px;
}

.content{
   background-color: white;
}

h1 h3 {
   font-family: 'Crimson Text', serif;
   font-weight: bold;
}

h1 {
   position: relative;
   color: #fff;    
   margin: 0 -20px 0px -20px;
   padding: 10px 30px; 
   text-shadow: 0 1px rgba(0,0,0,.8);
   background-color: #0440a0;
   background-image:  -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,.3)), to(rgba(255,255,255,0)));
   background-image:  -webkit-linear-gradient(rgba(255,255,255,.3), rgba(255,255,255,0));
   background-image:  -o-linear-gradient(rgba(255,255,255,.3), rgba(255,255,255,0));
   background-image:  linear-gradient(rgba(255,255,255,.3), rgba(255,255,255,0));
   -webkit-box-shadow: 0 2px 0 rgba(0,0,0,.3);
   box-shadow: 0 2px 0 rgba(0,0,0,.3);
}

h1:before, 
h1:after {
    content: '';
    position: absolute;
    border-style: solid;
    border-color: transparent;
    bottom: -10px;
}

h1:before {
    border-width: 0 10px 10px 0;
    border-right-color: #222;
    left: 0;
}

h1:after {
    border-width: 0 0 10px 10px;
    border-left-color: #222;
    right: 0;
}

@media (min-width: 768px) {
   .content{
      width: calc(100% - 40px);
      -webkit-transition: width 300ms linear;
      -o-transition: width 300ms linear;
      transition: width 300ms linear;
   }
   .content.open{
      width: calc(100% - 16.666667% - 50px);
      -webkit-transition: width 300ms linear;
      -o-transition: width 300ms linear;
      transition: width 300ms linear;
   }
}


